.main-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  pointer-events: none;

  transition: opacity 0.6s;
  opacity: 0;
}

body.menu-open .main-screen {
  opacity: 1;
  pointer-events: auto;
}
